.fullDivContainer {
  margin-top: 10vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.fullImage {
  width: 62vw;
  border-radius: 20px;
}

.fullDivBottom {
  height: 92%;
  width: 30vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 12vw;
  bottom: 4%;
  z-index: 1000;
  border-radius: 15px;
  gap: 2vh;
}

.fullDivWriting {
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3vh;
  padding: 1vw;
  text-align: center;
}

.fullButtonContainer {
  width: 18vw;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 2vw;
}

.fullBtn {
  height: 8vh;
  width: 10vw;
  min-width: 150px;
  margin-right: 2vw;
  font-size: 20px;
  font-weight: bold;
  border: none;
  border-radius: 25px;
}

.fullBtn:hover {
  cursor: pointer;
}

.fullWritingMain {
  font-size: 2vw;
}

@media (max-width: 1000px) {
  .fullImage {
    width: 0px;
  }

  .fullDivBottom {
    width: 90vw;
    height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    gap: 2vh;
    position: static;
    z-index: 0;
  }

  .fullWritingMain {
    font-size: 3vh;
  }
}
